'use client'

import { userRegister } from "@/redux/actions/userAuthAction"
import { useState } from "react"
import { useDispatch, useSelector } from "react-redux"




export default function Register() {
    
    const[data, setData] = useState({
        fullname: "",
        email: "",
        password: ""
    })

    const [loading, setLoading] = useState(false);

    const mssg = useSelector((state) => state.userRdcr.mssg)
   

    const dispatch = useDispatch()


    const handleSubmit = (e) => {
      e.preventDefault()

      setLoading(true)

      try {
        dispatch(userRegister(data))
      } finally {
        setLoading(false)
      }


    }


    
  return (
    <div className="container">
        <div className="text-center text-danger">


{mssg && ( <h3>{mssg}</h3>)}

</div>

      

<section id="register" className="register-section">
      <div className="container">
        <div className="row my-5 py-5">
          <div className="offset-md-3 col-md-6 my-5">
            <p className="display-3 fw-normal text-center">
              <span className="text-secondary">Register</span>
            </p>
            <form onSubmit={handleSubmit}>

            <div className="mb-3">
                <input
                  type="text"
                  className="form-control form-control-lg"
                  name="fullname"
                  value={data.fullname}
                  onChange={(e) => setData({...data, fullname: e.target.value})}

                  id="fullname"
                  placeholder="Your Full Name"
                />
              </div>

              <div className="mb-3">
                <input
                  type="email"
                  className="form-control form-control-lg"
                  name="email"
                  value={data.email}
                  onChange={(e) => setData({...data, email: e.target.value})}

                  id="email"
                  placeholder="Your Email Address"
                />
              </div>
              <div className="mb-3">
                <input
                  type="password"
                  className="form-control form-control-lg"
                  name="password"
                  value={data.password}
                  onChange={(e) => setData({...data, password: e.target.value})}

                  id="password"
                  placeholder="Your Password"
                />
              </div>
              
              <div className="d-grid gap-2">
                <button type="submit" className="btn btn-light btn-lg rounded-1">

                  {
                    loading ? (<span className="spinner-border spinner-border-sm" role="status">
                      Loading...


                    </span>) : "Register"
                  }
                  Register
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </section>
    </div>
  );
}
